:root{
  --color-primary:#34c94b
  --color-black:#121212
  --color-white:#fff
}
*,*::before,*::after{
  box-sizing: border-box;
}
*{
  margin: 0;
}
html,body{
  height: 100%;
}
body{
  background-color: #121212;
  font-family: "Poppins",sans-serif;
  color: #fff;
  /* overflow: hidden; */
}

img,svg{
  display: block;
  max-width: 100%;
}
