
.wrapped{
  display: flex;
  align-items: stretch;
  height: 48px;
}
.search{
  padding: 14px 20px;
  height: 100%;
  width: 502px;
  border-radius: 8px;
  border-top-right-radius: 0px;
  border-bottom-right-radius:0px ;
  border:1px solid #121212;
}
.searchbtn{
  border-radius: 0px 8px 8px 0px;
  padding: 13px 20px;
  height:100%;
  border:1px solid #121212;
  cursor: pointer;
  border-left: none;
}